import { MutationTree } from 'vuex';
import { State } from '@/stores/user/state';

export class Mutations implements MutationTree<State> {
    [key: string]: (state: State, payload: any) => any;

    public SetEmail = (state: State, email: string) => {
        state.Email = email;
        localStorage.setItem('email', email);
    }

    public SetPassword = (state: State, password: string) => {
        state.Password = password;
        localStorage.setItem('password', password);
    }

    public ClearUser = (state: State) => {
        state.Email = null;
        state.Password = null;
        localStorage.removeItem('password');
        localStorage.removeItem('email');
    }
}
