




















































import { Component, Vue, Watch, Prop } from 'vue-property-decorator';
import axios, { AxiosResponse } from 'axios';
import Message from '@/models/Message';

@Component
export default class Chatbot extends Vue {
    @Prop(String) private type!: string;
    private messages: Message[] = [];
    private currentMessage: string = '';
    private show: boolean = false;
    private triggerShowTimeOut: boolean = false;
    private apiUrl: string = '';

    public constructor() {
        super();

        if (process.env.NODE_ENV === 'development') {
            this.apiUrl = 'http://127.0.0.1:5000/chat';
        }

        if (process.env.NODE_ENV === 'production') {
            this.apiUrl = 'https://ce601-chatbot.azurewebsites.net/chat';
        }

        const messages = localStorage.getItem('messages');
        if (messages != null) {
            this.messages = JSON.parse(messages);
        }

        const showMessage = localStorage.getItem('showMessages');
        if (showMessage != null) {
            this.show = JSON.parse(showMessage);
        }
    }

    private get btnClass(): any {
        return {
            'btn-primary': this.type === 'primary',
            'btn-light': this.type === 'light',
            'text-primary': this.type === 'light',
        };
    }

    private get headerClass(): any {
        return {
            'bg-primary': this.type === 'primary',
            'bg-white': this.type === 'light',
            'text-primary': this.type === 'light',
            'text-white': this.type === 'primary',
        };
    }

    private toggle(): void {
        if (!this.show) {
            this.triggerShowTimeOut = true;
        }
        this.show = !this.show;
    }

    private scrollMessages(): void {
        const messageList = this.$refs.messageList;
        // @ts-ignore
        messageList.scrollTop = messageList.scrollHeight;
    }

    private updated(): void {
        if (this.show) {
            if (this.triggerShowTimeOut) {
                // @ts-ignore
                setTimeout(function() { this.scrollMessages(); }.bind(this), 400);
                this.triggerShowTimeOut = false;
            } else {
                this.scrollMessages();
            }
        }
    }

    private mounted(): void {
        if (this.show) {
            this.scrollMessages();
        }
    }

    private submitMessage(): void {
        this.messages.push(new Message('me', this.currentMessage, true));
        axios.post(this.apiUrl, {
            message: this.currentMessage,
        }).then((data: AxiosResponse<string>) => {
            this.messages.push(new Message('bot', data.data, false));
        });

        this.currentMessage = '';
    }

    @Watch('show')
    private showChanged() {
        const showMessages = JSON.stringify(this.show);
        localStorage.setItem('showMessages', showMessages);
    }

    @Watch('messages')
    private messagesChanged() {
        const newMessages = JSON.stringify(this.messages);
        localStorage.setItem('messages', newMessages);
    }
}
