import Vue from 'vue';
import Router from 'vue-router';
import Dashboard from './views/Dashboard.vue';
import Account from './views/Account.vue';
import Settings from './views/Settings.vue';
import Login from './views/Login.vue';
import Store from '@/store';
import UserGetterConstants from '@/stores/user/constants/UserGetterConstants';

Vue.use(Router);

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/login',
            name: 'login',
            component: Login,
            meta: {
            title: 'Login',
            },
        },
        {
            path: '/',
            name: 'dashboard',
            component: Dashboard,
            meta: {
            title: 'Dashboard',
            requiresAuthentication: true,
            },
        },
        {
            path: '/account',
            name: 'account',
            component: Account,
            meta: {
            title: 'Account',
            requiresAuthentication: true,
            },
        },
        {
            path: '/settings',
            name: 'settings',
            component: Settings,
            meta: {
            title: 'settings',
            requiresAuthentication: true,
            },
        },
    ],
});

router.beforeEach(async (to, from, next) => {
    const isLoggedIn = Store.getters[UserGetterConstants.IS_LOGGED_IN];

    if (!isLoggedIn && to.meta.requiresAuthentication) {
        next({ name: 'login' });
        return;
    }

    if (to.name === 'login' && isLoggedIn) {
        next({ name: 'dashboard' });
        return;
    }

    next();
});

export default router;
