export default class Message {
    public author: string;
    public text: string;
    public isoString: string;
    public isMe: boolean;

    public constructor(author: string, value: string, isMe: boolean) {
        this.author = author;
        this.text = value;
        this.isoString = new Date().toISOString();
        this.isMe = isMe;
    }
}
