import { ActionTree, ActionContext } from 'vuex';
import { State } from '@/stores/user/state';
import UserMutationConstants from '@/stores/user/constants/UserMutationConstants';
import User from '@/models/User';

export class Actions implements ActionTree<State, any> {
    [key: string]: ((injectee: ActionContext<State, any>, payload: any) => any);

    public Login = async (context: ActionContext<State, any>, payload: User) => {
        context.commit(UserMutationConstants.SET_EMAIL, payload.Email, { root: true });
        context.commit(UserMutationConstants.SET_PASSWORD, payload.Password, { root: true });
    }

    public Logout =  async (context: ActionContext<State, any>) => {
        context.commit(UserMutationConstants.CLEAR_USER, null, { root: true });
    }
}
